import React,{Component} from 'react';
import './profiles.css';
import stackoverflowIcon from '../images/stackoverflow.png';




class ProfileUnit extends Component{

    render(){
        return(
            <div className={"profiles-unit "+ this.props.className}>
                <img src={this.props.icon} height="30px" width="30px" className="profiles-icon"></img>
                <span className="profiles-unit__text">{this.props.displayName}</span>
            </div>
        )
    }
}
class Profiles extends Component{
    render(){
        return(
            <div className="tab">
                <div className="title" id="profiles">
                    Profiles
                    <div className="secondary-title">"There is no competition because nobody can be me."</div>
                </div>
                <div className="profiles-container">
                    <ProfileUnit displayName="Stack Overflow" icon={stackoverflowIcon} className="" />
                    <ProfileUnit displayName="Stack Overflow" icon={stackoverflowIcon} className="" />
                    <ProfileUnit displayName="Stack Overflow" icon={stackoverflowIcon} className="" />
                    <ProfileUnit displayName="Stack Overflow" icon={stackoverflowIcon} className="profiles-unit4" />
                    <ProfileUnit displayName="Stack Overflow" icon={stackoverflowIcon} className="profiles-unit5" />
                </div>
            </div>
        );
    }
}

export default Profiles;